'use client'
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Slider } from "@/components/ui/slider";

export function FilterSection({ 
  vegFilter, onVegFilterChange,
  maxSpiceLevel, onSpiceLevelChange,
  activeCategory, onCategoryChange,
  categories, onReset
}) {
  return (
    <div className="p-4 bg-gray-50 rounded-lg mb-4">
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <div>
          <Label htmlFor="diet-preference" className="text-sm font-medium">Diet Preference</Label>
          <Select value={vegFilter} onValueChange={onVegFilterChange}>
            <SelectTrigger id="diet-preference">
              <SelectValue placeholder="All Dishes" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Dishes</SelectItem>
              <SelectItem value="veg">Vegetarian Only</SelectItem>
              <SelectItem value="nonveg">Non-Vegetarian Only</SelectItem>
            </SelectContent>
          </Select>
        </div>

        <div>
          <Label className="text-sm font-medium">Max Spice Level: {maxSpiceLevel}</Label>
          <div className="pt-2">
            <Slider
              value={[maxSpiceLevel]}
              max={5}
              min={0}
              step={1}
              onValueChange={(value) => onSpiceLevelChange(value[0])}
            />
            <div className="flex justify-between mt-1 text-xs text-gray-500">
              <span>No Spice</span>
              <span>Max Spice</span>
            </div>
          </div>
        </div>

        <div>
          <Label htmlFor="category-select" className="text-sm font-medium">Category</Label>
          <Select
            value={activeCategory.toString()}
            onValueChange={(value) => onCategoryChange(parseInt(value))}
          >
            <SelectTrigger id="category-select">
              <SelectValue placeholder="All Categories" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="0">All Categories</SelectItem>
              {categories.map((category) => (
                <SelectItem key={category.id} value={category.id.toString()}>
                  {category.name}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </div>
      
      <div className="mt-4 flex justify-end">
        <button
          onClick={onReset}
          className="flex items-center gap-1 px-3 py-2 bg-gray-200 hover:bg-gray-300 rounded-md text-sm font-medium"
        >
          Reset Filters
        </button>
      </div>
    </div>
  );
}
